import React, { useState, useEffect } from "react";
import axios from "axios";
import BlogCard from "../components/BlogCard";

const Blogs = () => {
  const [blogs, setBlogs] = useState([]);
  const getAllBlogs = async () => {
    try {
      const { data } = await axios.get("/api/v1/blog/all-blogs");
      if (data?.success) {
        setBlogs(data?.blogs);
      }
    } catch (error) {
      
    }
  };
  useEffect(() => {
    getAllBlogs();
  }, []);
  return (
    <div>

{blogs &&
        blogs.map((blog) => (
          // Check if blog exists and has _id property before rendering
          blog && blog._id && (
            <BlogCard
              key={blog._id} // Make sure to add a unique key for each rendered component
              id={blog._id}
              isUser={localStorage.getItem('userId') === blog.user?._id}
              title={blog.title}
              description={blog.description}
              image={blog.image}
              username={blog.user && blog.user.username}
              time={blog.createdAt}
            />
          )
        ))}

      {/* {blogs && blogs.map(blog=>(
        <BlogCard 
        id={blog._id}
        isUser={localStorage.getItem('userId')===blog.user._id}

        title={blog.title } 
        description={blog.description}
        image={blog.image} 
        username={blog.user && blog.user.username}
       time={blog.createdAt} />


      ))} */}
      
    </div>
  );
};

export default Blogs;
